export default () => {
  /**
   * @name Helpers#isValid
   * @description
   * Validates recipe data
   * @type {Function}
   * @param {Object} recipe Recipe object
   * @returns {Boolean} True if recipe passes the validation
   */
  const isValid = recipe => recipe.account && recipe.amount.value && recipe.actions.isSelected();

  return {
    isValid,
  };
};



